.homeContainer{
    position:relative;
    padding-top:56.3px;
    padding-left: 70px;
    width:100%;
    height:100%;
    box-sizing: border-box;
    min-height: 100vh;
    background-color: var(--primary-color);
    color: var(--text-main);
    font-family: 'Roboto';
}

.categoryContainer{
    background-color: var(--primary-color);
    display:flex;
    padding:12px 20px;
    overflow-x:scroll;
    position: relative;
    --webkit-overflow-scrolling: touch;
}

.categoryContainer::-webkit-scrollbar{
    display: none;
}

.categoryTab{
    margin:0;
    font-weight: 400;
    font-size: 15px;
    padding:6px 14px;
    border-radius:10px;
    margin-right: 12px;
    work-break: keep-all;
    white-space: nowrap;
    background-color: var(--secondary-light-color);
    cursor: pointer;
}

.categoryTab:hover{
    background-color: var(--hover-text-color);
}

.categoryTab.active{
    background-color: var(--text-main);
    color: var(--primary-color);
}

.videoContainer{
    display:grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
    padding: 20px;
    column-gap:16px;
    row-gap:30px;
}

.changePadding{
    padding-left:240px;
}